import axios from "axios";

class Service {
  static getOptions(successCb, failureCb) {
    const query = `
    query countryQuery {
      countries {
        country
      }
    }`;
    axios
      .post("/graphql", { query: query })
      .then(result => {
        const { data } = result.data;
        successCb(data);
      })
      .catch(error => {
        failureCb(error);
      });
  }

  static async_getOptions() {
    return new Promise((resolve, reject) => {
      const query = `
      query countryQuery {
        countries {
          country
        }
      }`;
      axios
        .post("/graphql", { query: query })
        .then(result => {
          const { data } = result.data;
          resolve(data);
        })
        .catch(error => {
          reject(error);
        });
    });
  }

  static getData(countries = [], successCb, failureCb) {
    const query = `
    query statsQuery {
        stats(countries: ${JSON.stringify(countries)}) {
          country
          cases
          deaths
          active
          critical
          recovered
          todayCases
          todayDeaths
          casesPerOneMillion
        }
      }
    `;
    axios
      .post("/graphql", { query: query })
      .then(result => {
        const { data } = result.data;
        successCb(data);
      })
      .catch(error => {
        failureCb(error);
      });
  }

  static async_getData(countries = []) {
    return new Promise((resolve, reject) => {
      const query = `
      query statsQuery {
          stats(countries: ${JSON.stringify(countries)}) {
            country
            cases
            deaths
            active
            critical
            recovered
            todayCases
            todayDeaths
            casesPerOneMillion
          }
        }
      `;
      axios
        .post("/graphql", { query: query })
        .then(result => {
          const { data } = result.data;
          resolve(data);
        })
        .catch(error => {
          reject(error);
        });
    });
  }
}

export default Service;
