<template>
  <div id="app">
    <div class="app-container">
      <!-- autocomplete -->
      <app-autocomplete class="elevated" :world-countries="storeUserOptions" @select="onSelect"></app-autocomplete>
      <!-- results -->
      <template v-if="storeUserData.length > 0">
        <div v-for="(stats, index) of storeUserData" :key="index">
          <app-statistics
            class="elevated"
            :statistics="stats"
            :favourite="storeBookmarks.includes(stats.country.toLowerCase())"
            @pin="onPin"
            @unPin="onUnpin"
            @delete="onDelete"
          ></app-statistics>
        </div>
      </template>
      <!-- no data -->
      <template v-else>
        <div class="no-data">
          <div>No Countries Selected</div>
          <div>Add Some</div>
        </div>
      </template>
      <app-toast :duration="1500" :message="flashMessage"></app-toast>
    </div>
  </div>
</template>

<script>
import AppAutocomplete from "./components/AppAutocomplete";
import AppStatistics from "./components/AppStatistics";
import AppToast from "./components/AppToast";
import { mapGetters } from "vuex";

export default {
  name: "App",

  components: {
    AppAutocomplete,
    AppStatistics,
    AppToast
  },

  data() {
    return {
      flashMessage: ""
    };
  },

  computed: {
    ...mapGetters(["storeBookmarks", "storeUserData", "storeUserCountries", "storeUserOptions"])
  },

  methods: {
    onSelect(country) {
      this.$store.dispatch("addData", country);
    },

    onDelete(country) {
      this.$store.dispatch("removeData", country);
    },

    onPin(country) {
      this.$store.dispatch("pinData", country);
      this.flashMessage = `${country} added from Favourites`;
    },

    onUnpin(country) {
      this.$store.dispatch("unpinData", country);
      this.flashMessage = `${country} removed from Favourites`;
    }
  },

  created() {
    this.$store.dispatch("initialize");
  }
};
</script>

