class Storage {
  static getItems() {
    const data_serialized = localStorage.getItem("bookmarks");
    if (data_serialized) {
      return JSON.parse(data_serialized);
    } else {
      return null;
    }
  }

  static async_getItems() {
    return new Promise(resolve => {
      const data_serialized = localStorage.getItem("bookmarks");
      if (data_serialized) {
        resolve(JSON.parse(data_serialized));
      } else {
        resolve(null);
      }
    });
  }

  static setItems(items = []) {
    if (Array.isArray(items)) {
      localStorage.setItem("bookmarks", JSON.stringify(items));
      return true;
    } else {
      return false;
    }
  }

  static addItem(value) {
    let entry = value
      .toString()
      .trim()
      .toLowerCase();
    let data = this.getItems();
    if (!data) {
      data = [entry];
    } else if (!data.includes(entry)) {
      data = [...data, entry];
    } else {
      return false;
    }
    this.setItems(data);
    return true;
  }

  static removeItem(value) {
    let entry = value
      .toString()
      .trim()
      .toLowerCase();
    let data = this.getItems();
    if (!data) {
      return false;
    } else {
      data = data.filter(item => {
        return item !== entry;
      });
    }
    this.setItems(data);
    return true;
  }

  static clearItems() {
    localStorage.clear();
  }
}

export default Storage;
