import Vue from "vue";
import Vuex from "vuex";
import types from "./mutations";
import Storage from "../Storage";
import Service from "../Service";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    bookmarks: [],
    userData: [],
    userOptions: [],
    userCountries: new Set()
  },

  getters: {
    storeBookmarks: state => {
      return state.bookmarks;
    },

    storeUserData: state => {
      return state.userData;
    },

    storeUserOptions: state => {
      return state.userOptions;
    },

    storeUserCountries: state => {
      return Array.from(state.userCountries);
    }
  },

  mutations: {
    [types.BOOKMARKS_LOAD](state, payload) {
      Vue.set(state, "bookmarks", payload.bookmarks);
    },
    [types.USERDATA_LOAD](state, payload) {
      Vue.set(state, "userData", payload.data);
    },
    [types.USEROPTIONS_LOAD](state, payload) {
      Vue.set(state, "userOptions", payload.options);
    },
    [types.USERCOUNTRIES_LOAD](state, payload) {
      Vue.set(state, "userCountries", new Set(payload.countries));
    }
  },

  actions: {
    loadOptions: async ({ commit }) => {
      try {
        const options = await Service.async_getOptions();
        commit({
          type: "USEROPTIONS_LOAD",
          options: options.countries.map(item => item.country.toLowerCase())
        });
      } catch (error) {
        // TODO
        // set a timeout in the server
        // commit error
        console.log(error);
      }
    },

    loadBookmarks: async ({ commit }) => {
      try {
        const bookmarks = await Storage.async_getItems();
        commit({
          type: "BOOKMARKS_LOAD",
          bookmarks: bookmarks || []
        });
      } catch (error) {
        // set error
        console.log(error);
      }
    },

    loadData: async ({ state, dispatch, commit }) => {
      try {
        await dispatch("loadBookmarks");
        const { stats } = await Service.async_getData(state.bookmarks);
        commit({
          type: "USERDATA_LOAD",
          data: stats
        });
        commit({
          type: "USERCOUNTRIES_LOAD",
          countries: [...state.bookmarks]
        });
        // }
      } catch (error) {
        // set error
        console.log(error);
      }
    },

    initialize: async ({ dispatch }) => {
      try {
        await dispatch("loadOptions");
        await dispatch("loadData");
      } catch (error) {
        // set error
        console.log(error);
      }
    },

    addData: async ({ state, getters, commit }, country) => {
      const newCountry = country.trim().toLowerCase();
      if (!state.userCountries.has(newCountry)) {
        const newUserCountries = [...getters.storeUserCountries, newCountry];
        try {
          const { stats } = await Service.async_getData(newUserCountries);
          commit({
            type: "USERDATA_LOAD",
            data: stats
          });
          commit({
            type: "USERCOUNTRIES_LOAD",
            countries: newUserCountries
          });
        } catch (error) {
          // set error
          console.log(error);
        }
      }
    },

    removeData: ({ getters, commit }, country) => {
      const newUserData = [...getters.storeUserData].filter(item => {
        return item.country.toLowerCase() !== country.trim().toLowerCase();
      });
      commit({
        type: "USERDATA_LOAD",
        data: newUserData
      });
      const newUserCountries = [...getters.storeUserCountries].filter(item => {
        return item !== country.trim().toLowerCase();
      });
      commit({
        type: "USERCOUNTRIES_LOAD",
        countries: newUserCountries
      });
    },

    pinData: async ({ commit }, country) => {
      // TODO
      // sync or async?
      if (Storage.addItem(country.trim().toLowerCase())) {
        commit({
          type: "BOOKMARKS_LOAD",
          bookmarks: await Storage.async_getItems()
        });
      }
    },

    unpinData: async ({ commit }, country) => {
      // TODO
      // sync or async?
      if (Storage.removeItem(country.trim().toLowerCase())) {
        commit({
          type: "BOOKMARKS_LOAD",
          bookmarks: await Storage.async_getItems()
        });
      }
    }
  },
  modules: {}
});
